label {
    display: inline-block;
    margin: 10px 0;
}

input{
    border : 1px solid #dedede;
    border-radius: 10px;
    display: block;
    font-size: 16px;
    padding: 10px;
    width: 100%;
}

input:focus{
    outline: none;
}

.btn{
    background-color: #3208ec;
    color:#dedede;
    cursor: pointer;
    display: block;
    margin: 10px 0 30px;
    padding: 10px;
    font-size: 16px;
    width: 100%;
    border-radius: 10px;
    border: white solid;
}

